const initState = {
  status: false,
};

const authReducer = (state = initState, action) => {
  switch (action.type) {
    case "SET_AUTH_TRUE":
      return {
        ...state,
        status: true,
      };
    case "SET_AUTH_FALSE":
      return {
        ...state,
        status: false,
      };
    default:
      return state;
  }
};

export default authReducer;

