import {
  Routes,
  Route,
  BrowserRouter,
  Navigate,
  Outlet,
} from "react-router-dom";
import { useSelector } from "react-redux";
import Main from "./containers/main/Main"
import HomepageContainer from "./containers/homepage/HomepageContainer";
import TransactionsContainer from "./containers/chart/TransactionsContainer";
import DashboardContainer from "./containers/dashboard/DashboardContainer";
import ProfileContainer from "./containers/user/ProfileContainer";
import AuthContainer from "./containers/auth/AuthContianer";

export default function App() {
  return (
    <BrowserRouter>
      <Routes>
        {/* public pages */}
        <Route path="/" element={<Main />}>
          <Route index element={<HomepageContainer />} />
          <Route path="/transactions/:type" element={<TransactionsContainer />}/>
          <Route path="/transactions/dashboard" element={<DashboardContainer />}/>
          <Route path="/profile" element={<ProfileContainer />}/>
          <Route path="/login" element={<AuthContainer />}/>
          {/* private pages */}
          <Route path="/profile" element={<PrivateOutlet />}>
            

          </Route>



          
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

function PrivateOutlet() {
  const auth = useAuth();
  return auth === "true" ? <Outlet /> : <Navigate to="/login" />;
}


